import React from 'react'

import {
  CircularProgress,
  Select,
  MenuItem,
  Typography,
  FormControl,
  InputLabel,
} from '@material-ui/core'
import { makeStyles } from '@material-ui/styles'
import PropTypes from 'prop-types'

import { useRouteMatch, useHistory } from 'react-router-dom'

import useApi from 'utils/useApi'

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    alignSelf: 'stretch',
  },
}))

const SelectItem = ({ apiRoute, name, pathParameter, itemMapper }) => {
  const classes = useStyles()
  const { loading, data, error } = useApi(apiRoute)
  const match = useRouteMatch()
  const { params } = match
  const history = useHistory()

  if (loading) return <CircularProgress />

  if (error) {
    return <Typography>{`Error Loading ${name} Data!`}</Typography>
  }

  if (!data || !Array.isArray(data) || !data.length) {
    return <Typography>{`No ${name} Data Found!`}</Typography>
  }

  const handleRouteChange = e => {
    const itemId = e.target.value
    if (!itemId) return

    var urlParts = Object.entries(params)
      // filter out the parameter of the current path
      .filter(([paramName]) => paramName !== pathParameter)
      .map(([, paramValue]) => paramValue)
    urlParts.push(itemId)

    history.push('/' + urlParts.join('/'))
  }

  return (
    <FormControl color="secondary" className={classes.root}>
      <InputLabel>{`Select A ${name}`}</InputLabel>
      <Select value={params[pathParameter]} onChange={handleRouteChange}>
        {itemMapper(data).map(({ key, value, text }) => (
          <MenuItem key={key} value={value}>
            {text}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  )
}

SelectItem.propTypes = {
  apiRoute: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  pathParameter: PropTypes.string.isRequired,
  itemMapper: PropTypes.func.isRequired,
}

export default SelectItem
