import React from 'react'

import { Route } from 'react-router-dom'
import SelectItem from './SelectItem'

const SelectRoute = () => (
  <Route path="/:routeId?">
    <SelectItem
      name="Route"
      apiRoute="/routes"
      pathParameter="routeId"
      itemMapper={data =>
        data.map(({ Description, Route /*, ProviderID*/ }) => ({
          key: `Select-Route-Option-${Route}`,
          value: Route,
          text: Description,
        }))
      }
    />
  </Route>
)

export default SelectRoute
