import React from 'react'

import { CircularProgress, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/styles'
import { Route, useRouteMatch } from 'react-router-dom'
import useApi from 'utils/useApi'

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'stretch',
    padding: theme.spacing(2),
    border: `1px solid ${theme.palette.secondary.main}`,
    borderRadius: 15,
    alignSelf: 'stretch',
  },
  stop: {
    padding: theme.spacing(1),
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    '& > div': {
      backgroundColor: theme.palette.secondary.main,
      color: theme.palette.primary.main,
      borderRadius: '100%',
      margin: theme.spacing(1),
      height: 25,
      width: 25,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
  },
  sectionHeader: {
    color: theme.palette.secondary.main,
    alignSelf: 'center',
  },
}))

const ShowStops = () => {
  const classes = useStyles()
  const { params } = useRouteMatch()
  const { routeId, directionId } = params
  const url = `/stops/${routeId}/${directionId}`
  const { loading, data, error } = useApi(url)

  if (loading) return <CircularProgress />

  if (error) {
    return <Typography>Error Loading Stops!</Typography>
  }

  if (!data || !Array.isArray(data) || !data.length) {
    return <Typography>No Stops Found!</Typography>
  }

  return (
    <div className={classes.root}>
      <Typography className={classes.sectionHeader} variant="h5">
        Stops
      </Typography>
      {data.map(({ Text: text, Value: value }, index) => (
        <Typography key={`stop-${value}`} className={classes.stop}>
          <div className={classes.stopIndex}>{index + 1}</div>
          {text}
        </Typography>
      ))}
    </div>
  )
}

const ShowStopsRoute = () => (
  <Route path="/:routeId/:directionId">
    <ShowStops />
  </Route>
)

export default ShowStopsRoute
