import { useState, useEffect } from 'react'
import axios from 'axios'

const useApi = path => {
  const [loading, setLoading] = useState(false)
  const [data, setData] = useState(null)
  const [error, setError] = useState(null)

  const fetchData = async url => {
    var mounted = true
    setLoading(true)
    setData(null)
    setError(null)

    axios
      .get(url)
      .then(({ data }) => {
        if (!mounted) return
        setData(data)
        setLoading(false)
      })
      .catch(err => {
        if (!mounted) return
        setError(err)
        setLoading(false)
      })

    return () => {
      mounted = false
    }
  }

  useEffect(() => {
    const url = `https://svc.metrotransit.org/nextrip${path}`
    fetchData(url)
  }, [path])

  return {
    loading,
    data,
    error,
  }
}

export default useApi
