import React from 'react'

import { Route } from 'react-router-dom'
import SelectItem from './SelectItem'

const SelectDirection = () => (
  <Route
    path="/:routeId/:directionId?"
    render={({ match }) => (
      <SelectItem
        name="Direction"
        apiRoute={`/directions/${match.params.routeId}`}
        pathParameter="directionId"
        itemMapper={data =>
          data.map(({ Text, Value /*, ProviderID*/ }) => ({
            key: `Select-Direction-Option-${Value}`,
            value: Value,
            text: Text,
          }))
        }
      />
    )}
  ></Route>
)

export default SelectDirection
